import React from 'react'
import './stick.scss'

const AppStick = (props) => {
    const { title, description, buttonText, color } = props;
    const backgroundStyle = {
        backgroundColor: color,
      }

    return (
        <div className="stick" style={backgroundStyle}>
            <div className="left-content">
                <h3>{title}</h3>
                <p>{description}</p>
            </div>
            <button>{buttonText}</button>
        </div>
    )
}

export default AppStick
